import React, { Component } from 'react'

export default class menuIcon extends Component {
    constructor(props){
        super(props);
        this.state={
            iconStyle: "menuIcon",
            height: 0,
            visible: "hidden"
        }
        this.toggle=this.toggle.bind(this);
    }

    toggle(e) {
        if(this.state.iconStyle==="menuIcon"){
            this.setState({
                iconStyle: "change menuIcon",
                height: "auto",
                visible: "visible"
            })
        }else{
            this.setState({
                iconStyle: "menuIcon",
                height: 0,
                visible: "hidden"
            })
        }
        
    }
  render() {
    return (
      <div>
        

        <div className="slideMenu" style={{"height":this.state.height}}>
            <ul style={{"visibility":this.state.visible}}>
                <li><a href="/"><div>Home</div></a></li>
                <li><a href="#about"><div>About</div></a></li>
                <li><a href="/projects"><div>Projects</div></a></li>
                <li><a href="/contact"><div>Contact</div></a></li>
            </ul>
        </div>
        <div className={this.state.iconStyle} onClick={(e)=>this.toggle(e)}>
            <div className="bar1"></div>
            <div className="bar2"></div>
            <div className="bar3"></div>
        </div>
      </div>
    )
  }
}
